import { Component, NgZone, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiservicesService } from 'src/app/provider/apiservices.service';
import { DataTransferService } from 'src/app/services/data-transfer.service';
import { LocalStorageService } from 'src/app/services/local-storage.service';
import { URLS } from 'src/assets/constant';

@Component({
  selector: 'app-awarddashboard-detail',
  templateUrl: './awarddashboard-detail.page.html',
  styleUrls: ['./awarddashboard-detail.page.scss'],
})
export class AwarddashboardDetailPage implements OnInit {
  dashData: any;
  DashboardData: any;
  errorMessage: string;
  previousDashboard: any;

  constructor(private zone:NgZone,private apiservices:ApiservicesService,private stoargeServices:LocalStorageService,
    private sendData:DataTransferService,private router:Router) { }

  ngOnInit() {
    this.previousDashboard=this.sendData.alldata;
    this.awardDashboardFun();
  }

  awardDashboardFun(){
    this.zone.run(async () => {
     this.apiservices.showLoader();
     this.stoargeServices.getStorage().then(storedData=>{
      let apiKey = { 
        employee_id:storedData.employeeID,
        award_id:this.previousDashboard.auto_id,

      }
      this.apiservices.apiCallWithLoginToken(URLS.awardDashboardDetailApi, apiKey).subscribe((result) => {
        this.apiservices.hideLoader();
        this.dashData=result;
        if (result.status == 1) {
          this.DashboardData=result.data;
          this.errorMessage='';
        } else {
          this.errorMessage=result.message;
          this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
        }
        
      }, err => {
        this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
        this.apiservices.hideLoader();
      });
     
  
    });
     });
  }

  gotolist(item){
    this.sendData.alldata={mainData:this.DashboardData,nominationId:''};
    // this.sendData.alldata=this.DashboardData;
    this.router.navigate(['/award-employee-list']);
  }

}
